<template>
  <section v-if="bookInfo">
    <!-- <router-view></router-view> -->

    <button @click="$router.push('/mybook')">书架</button>
    
    <div class="info">
      <van-image
      width="70px"
      height="90px"
      fit="cover"
      lazy-load
      :src="bookInfo.cover | parseImageUrl"
    />
    <span>{{bookInfo.author}}</span>|
    <span>{{bookInfo.cat}}</span>|
    <span>{{bookInfo.wordCount}}</span>
    <span>追人气：{{bookInfo.latelyFollower}}</span>
    <span>读者留存率：{{bookInfo.retentionRatio}}%</span>
    <span>日更字数{{bookInfo.seriaLizeWordCount}}</span>
      <h3>{{ bookInfo.title }}</h3>

      <van-button
        v-if="isExistInMyBooks"
        type="danger"
        @click.native="delMyBooks(bookId)"
        >移出书架</van-button
      >
      <van-button v-else type="primary" @click.native="addMyBooks(bookInfo)"
        >加入书架</van-button
      >

      <van-button type="info" @click="$router.push('/read/' + bookId)"
        >开始阅读</van-button
      >

      <span>{{bookInfo.longIntro}}</span>
      <p @click="$router.push('/chapter/' + bookId)">
        目录: {{ bookInfo.lastChapter }}
      </p>
    </div>

    <ul class="raletions">
      <BookItem v-for="item in bookRaletions" :key="item.id" :item="item" />
    </ul>
  </section>
</template>

<script>
// https://novel.kele8.cn/book-info/:id
import {
  getBookInfo,
  getBookRaletions,
  getChapters,
  getChapterContent,
} from "../apis/apiBook";
import BookItem from "@/components/BookItem.vue";

// console.log(getBookInfo);

import { mapState, mapMutations } from "vuex";
import { Toast } from "vant";
import { Notify } from "vant";

export default {
  components: {
    BookItem,
  },
  props: ["bookId"],
  data: function () {
    return {
      bookInfo: null,
      bookRaletions: [],
    };
  },
  
  filters: {
    parseImageUrl: function (str) {
      return "//statics.zhuishushenqi.com" + str; 
    }
  },

  computed: {
    ...mapState(["myBooks", "allChapters"]),
    isExistInMyBooks: function () {
      return this.myBooks.some((item) => item._id == this.bookId);
    },

    currentChapter: function () {
      return this.$store.getters.currentChapter(this.bookId);
    },
  },
  methods: {
    ...mapMutations([
      "addMyBooks",
      "delMyBooks",
      "updateAllChapters",
      "updateCurrentChapterContent",
    ]),
  },

  created() {},

  beforeRouteEnter(to, from, next) {
    // ...
    // console.log("路由进入", to);

    // Toast.loading({
    //   message: "加载中...",
    //   forbidClick: true,
    // });

    // getBookInfo(to.params.bookId).then((res) => (vm.bookInfo = res.data));
    // getBookRaletions(to.params.bookId).then(
    //     (res) => (vm.bookRaletions = res.data.books)
    //   );

    Toast.loading({
      duration: 0,
      message: "加载中...",
      forbidClick: true,
    });
    Promise.all([
      getBookInfo(to.params.bookId),
      getBookRaletions(to.params.bookId),
    ])
      .then((res) => {
        //   console.log(res);
        next((vm) => {
          vm.bookInfo = res[0].data;
          vm.bookRaletions = res[1].data.books;
        });
      })
      .catch(() => {
        Notify("加载失败");
      })
      .finally(() => {
        Toast.clear();
      });
  },

  beforeRouteUpdate(to, from, next) {
    // ...
    // console.log("路由更新", to);

    // getBookInfo(to.params.bookId).then((res) => (this.bookInfo = res.data));
    // getBookRaletions(to.params.bookId).then(
    //   (res) => (this.bookRaletions = res.data.books)
    // );

    Toast.loading({
      duration: 0,
      message: "加载中...",
      forbidClick: true,
    });
    Promise.all([
      getBookInfo(to.params.bookId),
      getBookRaletions(to.params.bookId),
    ])
      .then((res) => {
        //   console.log(res);
        this.bookInfo = res[0].data;
        this.bookRaletions = res[1].data.books;
      })
      .catch(() => {
        Notify("加载失败");
      })
      .finally(() => {
        Toast.clear();
      });

    next();
  },

  // 跳出详情页面之前 判断 如果是 去到阅读页面 或者 章节页面 就先获取数据 再跳转

  beforeRouteLeave(to, from, next) {
    // ...
    // console.log(to);
    // if ((to.name === "Read" || to.name === "Chapter") && !this.currentChapters) {
    //   getChapters(this.bookId).then((res) => {
    //     // console.log(res);
    //     this.updateCurrentChapter(res);
    //     next();
    //   });
    // } else {
    //   next();
    // }
    // 首先判断 所有的章节里面 有没有当前书籍的 章节列表
    if (!this.currentChapter) {
      //
      if (to.name === "Chapter" || to.name === "Read") {
        // 获取章节
        Toast.loading({
          duration: 0,
          message: "加载中...",
          forbidClick: true,
        });
        getChapters(this.bookId).then((res) => {
          // 添加到所有章节
          this.updateAllChapters(res);
          Toast.clear();

          if (to.name === "Chapter") {
            // 如果是章节页面  直接跳转
            next();
          } else if (to.name === "Read") {
            // 如果是 阅读页面 先获取 第n章 的内容 之后在跳转
            // console.log(this.currentChapter.chapters[0].id);
            Toast.loading({
              duration: 0,
              message: "加载中...",
              forbidClick: true,
            });
            getChapterContent(this.currentChapter.chapters[0].link).then(
              (res) => {
                Toast.clear();
                this.updateCurrentChapterContent(res.data.chapter);
                next();
              }
            );
          }
        });
      }
    } else {
      next();
    }
  },
};
</script>



