<template>
  <li class="book-item" @click="$router.push('/book/' + item._id)">
    <van-image
      width="70px"
      height="90px"
      fit="cover"
      lazy-load
      :src="item.cover | parseImageUrl"
    />
    <!-- <img src="https://img01.yzcdn.cn/vant/cat.jpeg" alt=""> -->

    <div class="info">
      <h5 class="name">{{ item.title }}</h5>
      <p class="desc">
        {{item.shortIntro}}
      </p>
      <div class="category">
        <div>
          <span>{{item.minorCate}}</span>
          <span class="split">|</span>
          <span class="c-red">{{item.latelyFollower | parseLatelyFollower}}</span>
        </div>
        <i class="tag">{{item.majorCate}}</i>
      </div>
      <van-divider :style="{ borderColor: '#c4c4c4', margin: '5px 0' }" />
    </div>
  </li>
</template>

<script>
export default {
  props: ["item"],
  filters: {
    parseImageUrl: function (str) {
      return "//statics.zhuishushenqi.com" + str; 
    },
    parseLatelyFollower:function (value) {
      return value >=10000 ? parseFloat((value / 10000).toFixed(1)) + "万" : value;
    }
  },
};
</script>

<style lang="less">
li.book-item {
  padding-right: 10px;
  display: flex;
  .van-image {
    vertical-align: middle;
  }
  .info {
    flex: 1;
    margin-left: 10px;

    line-height: 20px;
    font-size: 12px;
    h5.name {
      line-height: 20px;
      font-size: 14px;
    }
    .desc {
      color: #888;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;

      margin: 5px 0;
    }
    .category {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        color: #666;
        &.split {
          margin: 0 5px;
        }
        &.c-red {
          color: #b83320;
        }
      }
      i.tag {
        font-size: 10px;
        font-style: normal;
        border: 1px solid #FEC224;
        color: #FEC224;
        line-height: 1.2em;
        padding: 0 3px;
        border-radius: 4px;
      }
    }
  }
}
</style>