<template>
  <section class="home">
    <header class="navbar">
      <van-nav-bar>
        <template #left>
          <van-image
            width="78"
            height="18"
            src="data:image/png;base64,"
          />
        </template>

        <template #title>
          <button
            :class="{ active: gender === 'male' }"
            @click="$router.push('/?gender=male')"
          >
            男生
          </button>
          <button
            :class="{ active: gender === 'female' }"
            @click="$router.push('/?gender=female')"
          >
            女生
          </button>
        </template>

        <template #right>
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
        </template>
      </van-nav-bar>

      <div class="search">
        <div class="search-box"
         @click="$router.push({name:'SearchPage',params:{}})">
          <van-swipe
          
            style="height: 30px; font-size:12px; color:#888"
            :loop="true"
            :autoplay="3000"
            vertical
            :show-indicators="false"
          >
            <van-swipe-item>鸿天神尊</van-swipe-item>
            <van-swipe-item>他从地狱回来</van-swipe-item>
            <van-swipe-item>出名太快怎么办</van-swipe-item>
            <van-swipe-item>重出千金：帝少的燃情宠妻</van-swipe-item>
          </van-swipe>

          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
        </div>
      </div>

    </header>

    <div class="banner">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item, index) in bannersMale" :key="index">
          <van-image width="100vw" height="40vw" v-bind:src="item.image"> </van-image>
        </van-swipe-item>
      </van-swipe>

      <van-grid :border="false">
        <van-grid-item
          v-for="(item, index) in bannerNavs"
          :key="index"
          :text="item.text"
          @click.native="item.path && $router.push(item.path)"
        >
          <van-image slot="icon" width="10vw" v-bind:src="item.icon"> </van-image>
        </van-grid-item>
      </van-grid>
      <van-divider :style="{ margin: 0 }" />

      <div class="lately-read">
        <span>最近阅读</span>|<span>暂无阅读记录</span>
      </div>
    </div>
    <template v-if="gender == 'male'">
      <!-- <HomeList :list="maleList.books.slice(0, 5)">男生热门</HomeList> -->
      <!-- <HomeList :list="maleHot">男生完结</HomeList> -->

      <HomeList
        v-for="item in maleList"
        :key="item.id"
        :list="item.books.slice(0, 5)"
        >男生{{ item.shortTitle }}</HomeList
      >
    </template>
    <template v-else>
      <HomeList
        v-for="item in femaleList"
        :key="item.id"
        :list="item.books.slice(0, 5)"
        >女生{{ item.shortTitle }}</HomeList
      >
      <!-- <HomeList :list="maleHot">女生完结</HomeList> -->
    </template>
  </section>
</template>

<script>
import HomeList from "@/components/HomeList.vue";
import { mapMutations, mapGetters, mapState } from "vuex";
//import SearchPage from "@/components/SearchPage.vue";
//import debounce from "lodash/debounce";
export default {
  name: "Home",
  components: {
    // HelloWorld
    HomeList,
  },
  data: function () {
    
    return {
      gender: "male",
      bannersMale: [
        {
          image:
            require('../assets/1.jpg')
        },
        {
          image:
            require('../assets/2.png')
        },
        {
          image:
            require('../assets/3.png')
        },
      ],
      bannerNavs: [
        {
          icon:
            require('../assets/书架.png'),
          text: "书架",
        },
        {
          icon:
            require('../assets/排行.png'),
          text: "排行",
          path: "/ranking",
        },
        {
          icon:
            require('../assets/VIP.png'),
          text: "VIP",
        },
        {
          icon:
            require('../assets/充值 .png'),
          text: "充值",
        },
      ],
      maleHot: [],
      suggests:[],
    };
  },
  computed: {
    ...mapState(["rankCategory", "maleList", "femaleList"]),
    ...mapGetters(["maleListIds", "femaleListIds"]),

    // // 分类前三个 => 移到vuex
    // maleListIds: function () {
    //   return this.rankCategory ? this.rankCategory.male.slice(0, 3) : [];
    // },
    // femaleListIds: function () {
    //   return this.rankCategory ? this.rankCategory.female.slice(0, 3) : [];
    // },
  },

  methods: {
    ...mapMutations([
      "updateRankCategory",
      "updateMaleList",
      "updateFemaleList",
    ]),
    getListByGender: function () {
      if (this.gender === "female") {
        !this.femaleList &&
          Promise.all(
            this.femaleListIds.map((item) => {
              return this.axios.get("https://novel.kele8.cn/rank/" + item._id);
            })
          ).then((res) =>
            this.updateFemaleList(res.map((r) => r.data.ranking))
          );
      } else {
        // !this.maleList &&
        //   this.axios
        //     .get("https://novel.kele8.cn/rank/" + this.maleListIds[0]._id)
        //     .then((res) => {
        //       // console.log(res.data);
        //       // this.maleList = res.data.ranking;
        //       this.updateMaleList(res.data.ranking);
        //     });

        !this.maleList &&
          Promise.all(
            this.maleListIds.map((item) => {
              return this.axios.get("https://novel.kele8.cn/rank/" + item._id);
            })
          ).then((res) => this.updateMaleList(res.map((r) => r.data.ranking)));
      }
    },
    
  },

  created() {
    this.gender = this.$route.query.gender || "male";

    // 先判断排行分类 是否存在
    if (!this.rankCategory) {
      // 获取排行榜分类
      this.axios.get("https://novel.kele8.cn/rank-category").then((res) => {
        // console.log(res);
        this.updateRankCategory(res.data);

        // 获取 排行分类成功
        this.getListByGender();
      });
    } else {
      this.getListByGender();
    }

    // this.axios
    //   .get("https://novel.kele8.cn/recommend/53115e30173bfacb4904897e")
    //   .then((res) => {
    //     // console.log(res.data.books);
    //     this.maleHot = res.data.books;
    //   });
  },

  // beforeRouteEnter (to, from, next) {
  //   // ...
  //   console.log(to, from);

  //   next()
  // },

  beforeRouteUpdate(to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    // console.log(to, from);
    this.gender = to.query.gender || "male";
    this.getListByGender();
    next();
  },

  watch: {
    // maleListIds: function () {
    //   console.log("变化了");
    // },
    // gender: function (n) {
    //   console.log(n);
    //   if (n === "female") {
    //     !this.femaleList &&
    //       this.axios
    //         .get("https://novel.kele8.cn/rank/" + this.femaleListIds[0]._id)
    //         .then((res) => {
    //           // console.log(res.data);
    //           this.femaleList = res.data;
    //         });
    //   } else {
    //     !this.maleList &&
    //       this.axios
    //         .get("https://novel.kele8.cn/rank/" + this.maleListIds[0]._id)
    //         .then((res) => {
    //           // console.log(res.data);
    //           this.maleList = res.data;
    //         });
    //   }
    // },
  },
};
</script>
<style lang="less">
#app .van-nav-bar {
  background: #b83320;
}
.home {
  .navbar {
    .van-nav-bar {
      .van-nav-bar__content {
        height: 50px;
        .van-nav-bar__title {
          button {
            background: transparent;
            height: 26px;
            // line-height: 26px;
            border: 1px solid white;
            font-size: 14px;
            color: white;
            &:first-child {
              border-radius: 26px 0 0 26px;
            }
            &:last-child {
              border-radius: 0 26px 26px 0;
            }
            &.active {
              background: white;
              color: #888;
            }
          }
        }
        .van-nav-bar__right {
          .van-image {
            &:first-child {
              margin-right: 10px;
            }
          }
        }
      }
    }
    .search {
      height: 40px;
      padding: 5px 10px;
      // border-bottom: 1px solid #dfdfdf;
      box-shadow: 0 1px 3px 0 #dfdfdf;
      .search-box {
        background: #cfcfcf;
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 10px;
        .van-swipe {
          .van-swipe-item {
            line-height: 30px;
          }
        }
      }
    }
  }

  .banner {
    .van-grid {
      // border-bottom: 1px solid lightgray;
    }
    .lately-read {
      color: #888;
      line-height: 40px;
      font-size: 14px;
      margin-bottom: 10px;
      background: white;
      span{
        margin: 0 15px;
      }
      span:nth-child(2){
        color: #b83320;
      }
    }
  }
}
</style>
