import Vue from "vue";
import Vuex from "vuex";
// import { getChapters } from "../apis/apiBook";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        rankCategory: null,
        maleList: null,
        femaleList: null,
        // allBookInfo:[],
        // allBookRaletions: []

        myBooks: [],

        // currentChapter: null,
        allChapters: [],
        currentChapterContent: null,
    },
    getters: {
        maleListIds: (state) => {
            return state.rankCategory
                ? state.rankCategory.male.slice(0, 3)
                : [];
        },
        femaleListIds: (state) => {
            return state.rankCategory
                ? state.rankCategory.female.slice(0, 3)
                : [];
        },

        currentChapter: (state) => (bookId) => {
            return state.allChapters.find((item) => item.book === bookId);
        },
    },
    mutations: {
        // rankMaleAdd: function(params) {
        // }
        updateRankCategory: function(state, payload) {
            state.rankCategory = payload;
        },
        updateMaleList: function(state, payload) {
            state.maleList = payload;
        },
        updateFemaleList: function(state, payload) {
            state.femaleList = payload;
        },
        addMyBooks: function(state, payload) {
            state.myBooks.push(payload);
        },
        delMyBooks: function(state, payload) {
            state.myBooks = state.myBooks.filter(
                (item) => item.book !== payload
            );
        },

        updateAllChapters: function(state, payload) {
            state.allChapters.push(payload);
        },

        updateCurrentChapterContent: function(state, payload) {
            state.currentChapterContent = payload;
        },
    },
    // actions: {
    //     getChaptersAction: function(context, payload) {
    //         getChapters(payload).then((res) => {
    //             context.commit("updateCurrentChapter", res);
    //         });
    //         // console.log(payload);
    //     },
    // },
    modules: {},
});
