<template>
  <div class="box">
    <div class="search" :to="{ path: '/SearchPage' }">
      <van-search v-model="value" 
      show-action placeholder="请输入搜索关键词">
        <template #action>
          <div>确定</div>
        </template>
      </van-search>
    </div>

    <div class="sra-lis">
      <ul>
        <li v-for="(item,index) in suggests" :key="index">{{item.title}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["suggests"],
  data: function () {
    return {
      value: "",
    };
  },
  watch: {
    value: function (newValue) {
      this.$emit("value-change", newValue);
      // console.log(newValue);
    },
  },
};
</script>

<style lang="less">
.box{
  background-color: #fff;
  padding:10px ;
  .search{
.van-search__content{
  background-color: #EBEBf0;
  border-radius: 55px;
}
.van-search__action{
  padding: 0 20px;
  color:#748389;
}
  }
  .sra-lis{
    li{
      line-height: 52px;
      margin: 0 15px;
    }
  }
}
</style>