<template>
  <section class="ranking">
    <van-row>
      <van-col
        span="8"
        :class="{ active: type === 'male' }"
        @click="$router.push('/ranking?type=male')"
      >
        <span>男频</span>
      </van-col>

         <van-col
        span="8"
        :class="{ active: type === 'press' }"
        @click="$router.push('/ranking?type=press')"
      >
        <span>排行</span></van-col
      >

      <van-col
        span="8"
        :class="{ active: type === 'female' }"
        @click="$router.push('/ranking?type=female')"
      >
        <span>女频</span></van-col
      >
     
    </van-row>


    <div class="list">
      <van-sidebar v-model="activeKey">
        <van-sidebar-item
          v-for="item in currentRankCategory"
          :key="item._id"
          :title="item.shortTitle"
          :to="{ path: '/ranking', query: { type, id: item._id } }"
          :replace="true"
        />
        <!-- <van-sidebar-item
        title="标签名称"
        :to="{ path: '/ranking', query: { type, id: 456 } }"
      />
      <van-sidebar-item
        title="标签名称"
        :to="{ path: '/ranking', query: { type, id: 789 } }"
      /> -->
      </van-sidebar>

      <ul class="right" ref="right" v-if="currentRankList">
        <BookItem
          v-for="item in currentRankList.books"
          :key="item.id"
          :item="item"
        />
      </ul>
      <ul class="right" v-else>
        <van-skeleton
          title
          avatar
          avatar-shape="square"
          avatar-size="70px"
          :row="3"
          v-for="n in 5"
          :key="n"
        />
      </ul>
    </div>
  </section>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import BookItem from "@/components/BookItem.vue";
export default {
  components: {
    BookItem,
  },
  data() {
    return {
      //   activeName: "b",
      type: "male",
      //   id: "",
      activeKey: 0,

      //   currentRankList: [],
      allRankList: [],
      id: "",
    };
  },

  computed: {
    ...mapState(["rankCategory"]),
    currentRankCategory: function () {
      if (this.rankCategory) {
        switch (this.type) {
          case "female":
            return this.rankCategory.female;
          case "press":
            return this.rankCategory.epub;
          default:
            return this.rankCategory.male;
        }
      } else {
        return [];
      }
    },
    currentRankList: function () {
      return this.allRankList.filter((item) => item._id == this.id)[0];
    },
    // id: function () {
    //   return this.currentRankCategory[this.activeKey]._id;
    // },

    // id: function () {
    //   return this.$route.query.id || this.currentRankCategory[0]._id;
    // },
  },

  beforeRouteEnter(to, from, next) {
    // ...
    next((vm) => {
      // 通过 `vm` 访问组件实例
      vm.type = to.query.type || "male";
      vm.id = to.query.id;
    });
  },

  beforeRouteUpdate(to, from, next) {
    // ...
    this.type = to.query.type;
    this.id = to.query.id || this.currentRankCategory[0]._id;

    var isExistRankListBycurrentId = this.allRankList.some(
      (item) => item._id === this.id
    );

    //   初次进入获取 第一个 路由没有 id 自己获取第一个为id
    console.log("xxx ", this.id);

    // 获取数据
    !isExistRankListBycurrentId &&
      this.axios.get("https://novel.kele8.cn/rank/" + this.id).then((res) => {
        //   this.currentRankList = res.data.ranking;
        this.allRankList.push(res.data.ranking);
      });

    next();
  },

  created() {
    // 先判断排行分类 是否存在
    if (!this.rankCategory) {
      // 获取排行榜分类
      this.axios.get("https://novel.kele8.cn/rank-category").then((res) => {
        // console.log(res);
        this.updateRankCategory(res.data);

        //   初次进入获取 第一个
        this.id = this.$route.query.id || this.currentRankCategory[0]._id;

        // 根据 路由id 获取数据
        this.axios.get("https://novel.kele8.cn/rank/" + this.id).then((res) => {
          //   this.currentRankList = res.data.ranking;
          this.allRankList.push(res.data.ranking);
        });

        //  根据id 获取到是第几个高亮
        this.activeKey = this.currentRankCategory.findIndex(
          (item) => item._id === this.id
        );
      });
    }
  },

  methods: {
    ...mapMutations(["updateRankCategory"]),
  },

  watch: {
    type: function () {
      this.activeKey = 0;
      if (this.$refs.right) {
        this.$refs.right.scrollTop = 0;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.ranking {
  .van-row {
    .van-col {
      display: flex;
      justify-content: center;
      font-size: 14px;
      span {
        padding: 0 3px;
        line-height: 46px;
      }
      &.active {
        color: red;
        span {
          border-bottom: 2px solid red;
        }
      }
    }
  }
  .list {
    display: flex;
    .van-sidebar {
      height: calc(100vh - 48px);
      overflow-y: auto;
    }
    .right {
      background: white;
      flex: 1;
      height: calc(100vh - 48px);
      overflow-y: auto;
    }
  }
}
</style>